import {
    Button,
    Container,
    FormControl,
    Grid, InputAdornment,
    ListItem,
    ListItemText,
    Paper,
    TextField,
    Typography
} from "@mui/material";
import Web3 from "web3";
import rewards_abi from "../abis/rewards_locker.json";
import coineusone_abi from "../abis/coineusone_abi.json";
import erc20_abi from "../abis/IERC20.json";

import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {makeBatchRequest} from "../utils/promisify";
import {CoineusCryptoFormat} from "../utils/currency_format";
import {formatWeiToNumber} from "../utils/format";
import CoineusLoading from "../components/CoineusLoading";

const COINEUS_FUND_ADDRESS = "0xb80267eA7fa368374ee4d4Bf10044778232AdEFe";
const FUD_FUND_ADDRESS = "0x3b551c174098cDAA3A3220dfF34c822AB59B8e87";

export default function RewardsLocker() {

    const {wallet, rpc} = useSelector(state => state.coineus);

    const [data, setData] = useState({});
    const [dataFUSE, setDataFUSE] = useState({});
    const [pending, setPending] = useState(false);
    const [amount, setAmount] = useState("");

    const getInfo = () => {
        const web3AVAX = new Web3(rpc.avax);

        const fud_contract = new web3AVAX.eth.Contract(rewards_abi, "0xb930131f9b73e229c1ae1cf67409e99336043dc2");
        const coineus_contract = new web3AVAX.eth.Contract(rewards_abi, "0x2873b157ba89812ddf297719b66585a3aa836599");

        const calls = [
            fud_contract.methods.availableRewards().call,
            fud_contract.methods.claimedRewardsTotal().call,
            fud_contract.methods.lockedTokensAmount().call,
            fud_contract.methods.isClaimer(wallet.address).call,
            coineus_contract.methods.availableRewards().call,
            coineus_contract.methods.claimedRewardsTotal().call,
            coineus_contract.methods.lockedTokensAmount().call,
            coineus_contract.methods.isClaimer(wallet.address).call
        ];

        makeBatchRequest(web3AVAX, calls).then(resp => {
            let [
                fud_available,
                fud_claimed,
                fud_locked,
                fud_claimer,
                coineus_available,
                coineus_claimed,
                coineus_locked,
                coineus_claimer
            ] = resp;

            setData({
                fud_available,
                fud_claimed,
                fud_locked,
                fud_claimer,
                coineus_available,
                coineus_claimed,
                coineus_locked,
                coineus_claimer
            })

        });


        const web3FUSE = new Web3(rpc.fuse);

        const fusd_contract = new web3FUSE.eth.Contract(erc20_abi, "0x249BE57637D8B013Ad64785404b24aeBaE9B098B");
        const coineusone_contract = new web3FUSE.eth.Contract(coineusone_abi, "0x7042807d547A2de0720421a6d2f629c9EB43E73D");

        const callsFUSE = [
            fusd_contract.methods.balanceOf(coineusone_contract._address).call
        ]

        makeBatchRequest(web3FUSE, callsFUSE).then(resp => {
            let [
                fusd_balance_coineusone
            ] = resp;

            setDataFUSE({
                fusd_balance_coineusone
            })

        })
    }

    const claim = (contract) => {

        const web3AVAX = new Web3(window.provider);

        const _contract = new web3AVAX.eth.Contract(rewards_abi, contract);

        _contract.methods.getRewards()
            .send({from: wallet.address})
            .once('transactionHash', function (tx) {
                setPending(true)
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {
                setPending(false);
                getInfo();
            });
    }

    const claimFUSD = () => {

        const web3FUSE = new Web3(window.provider);
        const coineusone_contract = new web3FUSE.eth.Contract(coineusone_abi, "0x7042807d547A2de0720421a6d2f629c9EB43E73D");

        coineusone_contract.methods.claimRandomToken("0x249BE57637D8B013Ad64785404b24aeBaE9B098B")
            .send({from: wallet.address})
            .once('transactionHash', function (tx) {
                setPending(true)
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {
                setPending(false);
                getInfo();
            });

    }

    const rCNSONEToEveryone = () => {
        const web3FUSE = new Web3(window.provider);
        const coineusone_contract = new web3FUSE.eth.Contract(coineusone_abi, "0x7042807d547A2de0720421a6d2f629c9EB43E73D");

        coineusone_contract.methods.rCNSONEToEveryone(web3FUSE.utils.toBN(amount * (10**18)))
            .send({from: wallet.address})
            .once('transactionHash', function (tx) {
                setPending(true)
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {
                setPending(false);
                getInfo();
            });
    }

    useEffect(getInfo, [wallet.address]);

    return <Container maxWidth="sm">
        <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
            {
                wallet.address === FUD_FUND_ADDRESS && <Grid item xs={12}>
                    <Paper style={{padding: 20}}>
                        <Typography variant="h6">FUD Rewards Locker</Typography>
                        <ListItem disablePadding>
                            <ListItemText
                                primary={CoineusCryptoFormat(formatWeiToNumber(data.fud_locked)) + " FUD"}
                                secondary="Locked"
                            />
                        </ListItem>
                        <ListItem disablePadding>
                            <ListItemText
                                primary={CoineusCryptoFormat(formatWeiToNumber(data.fud_claimed)) + " AVAX"}
                                secondary="Claimed All-time"
                            />
                        </ListItem>
                        <ListItem disablePadding>
                            <ListItemText
                                primary={CoineusCryptoFormat(formatWeiToNumber(data.fud_available)) + " AVAX"}
                                secondary="Claimable"
                            />
                        </ListItem>
                        {
                            data.fud_claimer &&
                            <Button variant="contained" disabled={wallet.chain !== 43114} fullWidth style={{marginTop: 20}}
                                    onClick={() => claim("0xb930131f9b73e229c1ae1cf67409e99336043dc2")}>Claim</Button>
                        }
                    </Paper>
                </Grid>
            }


            {
                wallet.address === COINEUS_FUND_ADDRESS && <Grid item xs={12}>
                    <Paper style={{padding: 20}}>
                        <Typography variant="h6">Coineus Rewards Locker (FUD)</Typography>
                        <ListItem disablePadding>
                            <ListItemText
                                primary={CoineusCryptoFormat(formatWeiToNumber(data.coineus_locked)) + " FUD"}
                                secondary="Locked"
                            />
                        </ListItem>
                        <ListItem disablePadding>
                            <ListItemText
                                primary={CoineusCryptoFormat(formatWeiToNumber(data.coineus_claimed)) + " WAVAX"}
                                secondary="Claimed All-time"
                            />
                        </ListItem>
                        <ListItem disablePadding>
                            <ListItemText
                                primary={CoineusCryptoFormat(formatWeiToNumber(data.coineus_available)) + " WAVAX"}
                                secondary="Claimable"
                            />
                        </ListItem>
                        {
                            data.coineus_claimer &&
                            <Button variant="contained" disabled={wallet.chain !== 43114} fullWidth style={{marginTop: 20}}
                                    onClick={() => claim("0x2873b157ba89812ddf297719b66585a3aa836599")}>Claim</Button>
                        }
                    </Paper>
                </Grid>
            }


            {
                wallet.address === COINEUS_FUND_ADDRESS && <Grid item xs={12}>
                    <Paper style={{padding: 20}}>
                        <Typography variant="h6">Coineus ONE fUSD</Typography>
                        <ListItem disablePadding>
                            <ListItemText
                                primary={CoineusCryptoFormat(formatWeiToNumber(dataFUSE.fusd_balance_coineusone)) + " fUSD"}
                                secondary="Claimable"
                            />
                        </ListItem>
                        <Button variant="contained"
                                disabled={wallet.chain !== 122} fullWidth style={{marginTop: 20}}
                                onClick={claimFUSD}>Claim</Button>
                    </Paper>
                </Grid>
            }


            {
                wallet.address === COINEUS_FUND_ADDRESS && <Grid item xs={12}>
                    <Paper style={{padding: 20}}>
                        <Typography variant="h6">Reflect CNSONE</Typography>
                        <FormControl fullWidth style={{marginBottom: 20, marginTop: 20}}>
                            <TextField
                                autoComplete="off"
                                type="number"
                                value={amount}
                                placeholder="Enter Amount"
                                onChange={(ev) => {
                                    setAmount(ev.target.value)
                                }}
                            />
                        </FormControl>
                        <Button fullWidth variant="contained"
                                disabled={!amount}
                                onClick={rCNSONEToEveryone}>REFLECT CNSONE TO EVERYONE</Button>
                    </Paper>
                </Grid>
            }
        </Grid>

        <CoineusLoading open={pending} label="Transaction Pending"/>
    </Container>
}