import {
    Avatar,
    Container,
    Grid, List,
    ListItem,
    ListItemAvatar,
    ListItemText,
    Paper,
} from "@mui/material";
import Web3 from "web3";
import rewards_abi from "../abis/rewards_locker.json";
import coineus_abi from '../abis/coineus.json';

import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {makeBatchRequest} from "../utils/promisify";
import {CoineusCryptoFormat, CoineusUSDFormat} from "../utils/currency_format";
import {formatWeiToNumber} from "../utils/format";
import consensus_abi from "../abis/consensus_abi.json";
import {makeStyles} from "@mui/styles";
import clsx from "clsx";
import dividend_distributor_abi from "../abis/DividendDistributor.json";

const coineus_fund_address = "0xb80267eA7fa368374ee4d4Bf10044778232AdEFe";
const assurance_fund_address = "0x09501F8Ca9476d0c516E121FB4dCE4B7b2301d51";
const coineus_validator_address = "0x53e2033F86C1a54bEbAE893DC8C9fAe179b3Faa2";
const consensusAddress = "0x3014ca10b91cb3D0AD85fEf7A3Cb95BCAc9c0f79";

const useStyles = makeStyles((theme) => ({
    networkChip: {
        borderRadius: "50%",
        marginLeft: 30,
        position: 'absolute',
        top: 42,
        height: 16,
        width: 16,
        display: 'inline-block',
        backgroundSize: 'cover',
        '&.fuse': {
            backgroundImage: `url('https://coineus.app/assets/tokens/fuse/logo.png')`,
        },
        '&.avax': {
            backgroundImage: `url('https://coineus.app/assets/tokens/avax/logo.png')`,
        },
        '&.bnb': {
            backgroundImage: `url('https://coineus.app/assets/tokens/bnb/logo.png')`,
        },
        '&.eth': {
            backgroundImage: `url('https://coineus.app/assets/tokens/eth/logo.png')`,
        }
    }
}));

export default function CoineusPortfolio() {

    const classes = useStyles();

    const {wallet, rpc} = useSelector(state => state.coineus);

    const [data, setData] = useState({});

    const [fuseData, setFuseData] = useState({});

    useEffect(() => {

        const web3FUSE = new Web3(rpc.fuse);
        const consensusContract = new web3FUSE.eth.Contract(consensus_abi, consensusAddress);

        const callsF = [
            web3FUSE.eth.getBalance(coineus_fund_address),
            web3FUSE.eth.getBalance(assurance_fund_address),
            consensusContract.methods.delegatedAmount(coineus_fund_address, coineus_validator_address).call,
            consensusContract.methods.delegatedAmount(assurance_fund_address, coineus_validator_address).call,
        ];

        makeBatchRequest(web3FUSE, callsF).then(resp => {
            let [
                coineus_fund_balance,
                assurance_fund_balance,
                coineus_fund_staked,
                assurance_fund_staked
            ] = resp;

            coineus_fund_balance = coineus_fund_balance / (10 ** 18);
            assurance_fund_balance = assurance_fund_balance / (10 ** 18);
            coineus_fund_staked = coineus_fund_staked / (10 ** 18);
            assurance_fund_staked = assurance_fund_staked / (10 ** 18);

            setFuseData(
                {
                    coineus_fund_balance,
                    assurance_fund_balance,
                    coineus_fund_staked,
                    assurance_fund_staked
                }
            )
        })


        const web3AVAX = new Web3(rpc.avax);
        //rewards
        const coineus_fud_locker = new web3AVAX.eth.Contract(rewards_abi, "0x2873b157ba89812ddf297719b66585a3aa836599");
        //holdings
        const fook_contract = new web3AVAX.eth.Contract(coineus_abi, "0x95923F63dA09B4f7520f7C65a31F318D8228B744");
        const hedge_fund_contract = new web3AVAX.eth.Contract(coineus_abi, "0xe387cF241B6685D405C575ce35db78ed4844B34a");
        const usdc_contract = new web3AVAX.eth.Contract(coineus_abi, '0xB97EF9Ef8734C71904D8002F8b6Bc66Dd9c48a6E');

        const burnit_contract = new web3AVAX.eth.Contract(coineus_abi, '0x9CcAd19669259C1FBF13FDa959400C871b3d0E00');
        const burnit_dividends_contract = new web3AVAX.eth.Contract(dividend_distributor_abi, '0x32b82215a5ebb48acf7faadded562bc175716920');

        const dgnx_contract = new web3AVAX.eth.Contract(coineus_abi, '0x51e48670098173025C477D9AA3f0efF7BF9f7812');

        const calls = [
            coineus_fud_locker.methods.claimedRewardsTotal().call,
            coineus_fud_locker.methods.lockedTokensAmount().call,

            fook_contract.methods.balanceOf(coineus_fund_address).call,
            fook_contract.methods.getAccountDividendsInfo(coineus_fund_address).call,

            hedge_fund_contract.methods.balanceOf(coineus_fund_address).call,
            hedge_fund_contract.methods.getAccountDividendsInfo(coineus_fund_address).call,

            usdc_contract.methods.balanceOf(coineus_fund_address).call,

            burnit_contract.methods.balanceOf(coineus_fund_address).call,
            burnit_dividends_contract.methods.shares(coineus_fund_address).call,

            dgnx_contract.methods.balanceOf(coineus_fund_address).call,
        ];

        makeBatchRequest(web3AVAX, calls).then(resp => {
            let [
                coineus_claimed,
                coineus_locked,

                coineus_fund_fook_balance,
                coineus_fund_fook_dividends,

                coineus_fund_hedge_fund_balance,
                coineus_fund_hedge_fund_dividends,

                coineus_fund_usdc_balance,

                coineus_fund_burnit_balance,
                coineus_fund_burnit_shares,

                coineus_fund_dgnx_balance

            ] = resp;

            setData({
                coineus_claimed,
                coineus_locked,

                coineus_fund_fook_balance,
                coineus_fund_fook_dividends: coineus_fund_fook_dividends[4] / (10 ** 6),

                coineus_fund_hedge_fund_balance,
                coineus_fund_hedge_fund_dividends: coineus_fund_hedge_fund_dividends[4] / (10 ** 18),

                coineus_fund_usdc_balance: coineus_fund_usdc_balance / (10 ** 6),


                coineus_fund_burnit_balance,
                coineus_fund_burnit_dividends: coineus_fund_burnit_shares[2] / (10**6),

                coineus_fund_dgnx_balance
            })

        });

    }, []);

    const totalValue = (formatWeiToNumber(data.coineus_fund_hedge_fund_balance) * wallet.prices.avax?.['0xe387cF241B6685D405C575ce35db78ed4844B34a']?.inUSD) +
        (formatWeiToNumber(data.coineus_fund_fook_balance) * wallet.prices.avax?.['0x95923F63dA09B4f7520f7C65a31F318D8228B744']?.inUSD) +
        ((data.coineus_fund_burnit_balance / (10**9)) * wallet.prices.avax?.['0x9CcAd19669259C1FBF13FDa959400C871b3d0E00']?.inUSD) +
        (formatWeiToNumber(data.coineus_locked) * wallet.prices.avax?.['0x80363cc2CAa2545554d155031e47049ee2F98f33']?.inUSD) +
        (
            (
                fuseData.coineus_fund_balance +
                fuseData.coineus_fund_staked +
                fuseData.assurance_fund_balance +
                fuseData.assurance_fund_staked
            ) * wallet.prices.fuse?.native?.inUSD
        ) +
        data.coineus_fund_usdc_balance

    return <Container maxWidth="sm">
        <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>

            <Grid item xs={12}>
                <Paper style={{color: '#000', backgroundColor: '#fbca00'}}>
                    <ListItem>
                        <ListItemText
                            primary={CoineusUSDFormat(totalValue)}
                            secondary="Total Fund Value"
                            primaryTypographyProps={{fontWeight: 700, fontSize: '2.125rem', textAlign: 'right'}}
                            secondaryTypographyProps={{color: '#000', textAlign: 'right'}}
                        />
                    </ListItem>
                </Paper>
            </Grid>

            <Grid item xs={12}>
                <Paper>
                    <List>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar
                                    src="https://coineus.app/assets/tokens/fuse/logo.png"/>
                            </ListItemAvatar>
                            <ListItemText
                                primary="FUSE"
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Fund Holdings"
                            />
                            <ListItemText
                                primary={
                                    CoineusUSDFormat((
                                        fuseData.coineus_fund_balance +
                                        fuseData.coineus_fund_staked +
                                        fuseData.assurance_fund_balance +
                                        fuseData.assurance_fund_staked
                                    ) * wallet.prices.fuse?.native?.inUSD)
                                }
                                secondary="USD Value"
                                primaryTypographyProps={{fontWeight: 700, textAlign: 'right'}}
                                secondaryTypographyProps={{textAlign: 'right'}}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemText
                                primary={CoineusCryptoFormat(fuseData.coineus_fund_balance + fuseData.assurance_fund_balance) + " FUSE"}
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Wallet"
                            />
                            <ListItemText
                                primary={CoineusCryptoFormat(fuseData.coineus_fund_staked + fuseData.assurance_fund_staked) + " FUSE"}
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Staked"
                            />
                        </ListItem>
                    </List>
                </Paper>
            </Grid>

            <Grid item xs={12}>
                <Paper>
                    <List>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar
                                    src="https://coineus.app/assets/tokens/avax/0xB97EF9Ef8734C71904D8002F8b6Bc66Dd9c48a6E/logo.png"/>
                                <span className={clsx(classes.networkChip, 'avax')}/>
                            </ListItemAvatar>
                            <ListItemText
                                primary="USDC"
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Fund Holdings"
                            />
                            <ListItemText
                                primary={CoineusUSDFormat(data.coineus_fund_usdc_balance)}
                                primaryTypographyProps={{fontWeight: 700, textAlign: 'right'}}
                                secondary="USD Value"
                                secondaryTypographyProps={{textAlign: 'right'}}
                            />
                        </ListItem>
                    </List>
                </Paper>
            </Grid>

            <Grid item xs={12}>
                <Paper>
                    <List>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar
                                    src="https://coineus.app/assets/tokens/avax/0x80363cc2CAa2545554d155031e47049ee2F98f33/logo.png"/>
                                <span className={clsx(classes.networkChip, 'avax')}/>
                            </ListItemAvatar>
                            <ListItemText
                                primary="FUD"
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Rewards Locker"
                            />
                            <ListItemText
                                primary={CoineusUSDFormat(formatWeiToNumber(data.coineus_locked) * wallet.prices.avax?.['0x80363cc2CAa2545554d155031e47049ee2F98f33']?.inUSD)}
                                primaryTypographyProps={{fontWeight: 700, textAlign: 'right'}}
                                secondary="USD Value"
                                secondaryTypographyProps={{textAlign: 'right'}}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemText
                                primary={CoineusCryptoFormat(formatWeiToNumber(data.coineus_locked)) + " FUD"}
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Locked"
                            />
                            <ListItemText
                                primary={CoineusCryptoFormat(formatWeiToNumber(data.coineus_claimed)) + " WAVAX"}
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Earned All-time"
                            />
                        </ListItem>
                    </List>
                </Paper>
            </Grid>

            <Grid item xs={12}>
                <Paper>
                    <List>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar
                                    src="https://coineus.app/assets/tokens/avax/0x51e48670098173025C477D9AA3f0efF7BF9f7812/logo.png"/>
                                <span className={clsx(classes.networkChip, 'avax')}/>
                            </ListItemAvatar>
                            <ListItemText
                                primary="DGNX"
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Fund Holdings"
                            />
                            <ListItemText
                                primary={CoineusUSDFormat(formatWeiToNumber(data.coineus_fund_dgnx_balance) * wallet.prices.avax?.['0x51e48670098173025C477D9AA3f0efF7BF9f7812']?.inUSD)}
                                primaryTypographyProps={{fontWeight: 700, textAlign: 'right'}}
                                secondary="USD Value"
                                secondaryTypographyProps={{textAlign: 'right'}}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemText
                                primary={CoineusCryptoFormat(formatWeiToNumber(data.coineus_fund_dgnx_balance)) + " DGNX"}
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Balance"
                            />
                        </ListItem>
                    </List>
                </Paper>
            </Grid>

            <Grid item xs={12}>
                <Paper>
                    <List>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar
                                    src="https://coineus.app/assets/tokens/avax/0x9CcAd19669259C1FBF13FDa959400C871b3d0E00/logo.png"/>
                                <span className={clsx(classes.networkChip, 'avax')}/>
                            </ListItemAvatar>
                            <ListItemText
                                primary="BRN"
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Fund Holdings"
                            />
                            <ListItemText
                                primary={CoineusUSDFormat((data.coineus_fund_burnit_balance / (10**9)) * wallet.prices.avax?.['0x9CcAd19669259C1FBF13FDa959400C871b3d0E00']?.inUSD)}
                                primaryTypographyProps={{fontWeight: 700, textAlign: 'right'}}
                                secondary="USD Value"
                                secondaryTypographyProps={{textAlign: 'right'}}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemText
                                primary={CoineusCryptoFormat((data.coineus_fund_burnit_balance / (10**9))) + " BRN"}
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Balance"
                            />
                            <ListItemText
                                primary={CoineusCryptoFormat((data.coineus_fund_burnit_dividends)) + " USDC"}
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Earned All-time"
                            />
                        </ListItem>
                    </List>
                </Paper>
            </Grid>

            <Grid item xs={12}>
                <Paper>
                    <List>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar
                                    src="https://coineus.app/assets/tokens/avax/0x95923F63dA09B4f7520f7C65a31F318D8228B744/logo.png"/>
                                <span className={clsx(classes.networkChip, 'avax')}/>
                            </ListItemAvatar>
                            <ListItemText
                                primary="FOOK"
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Fund Holdings"
                            />
                            <ListItemText
                                primary={CoineusUSDFormat(formatWeiToNumber(data.coineus_fund_fook_balance) * wallet.prices.avax?.['0x95923F63dA09B4f7520f7C65a31F318D8228B744']?.inUSD)}
                                primaryTypographyProps={{fontWeight: 700, textAlign: 'right'}}
                                secondary="USD Value"
                                secondaryTypographyProps={{textAlign: 'right'}}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemText
                                primary={CoineusCryptoFormat(formatWeiToNumber(data.coineus_fund_fook_balance)) + " FOOK"}
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Balance"
                            />
                            <ListItemText
                                primary={CoineusCryptoFormat(data.coineus_fund_fook_dividends) + " USDC"}
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Earned All-time"
                            />
                        </ListItem>
                    </List>
                </Paper>
            </Grid>

            <Grid item xs={12}>
                <Paper>
                    <List>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar
                                    src="https://coineus.app/assets/tokens/avax/0xe387cF241B6685D405C575ce35db78ed4844B34a/logo.png"/>
                                <span className={clsx(classes.networkChip, 'avax')}/>
                            </ListItemAvatar>
                            <ListItemText
                                primary="Hedge Fund"
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Fund Holdings"
                            />
                            <ListItemText
                                primary={CoineusUSDFormat(formatWeiToNumber(data.coineus_fund_hedge_fund_balance) * wallet.prices.avax?.['0xe387cF241B6685D405C575ce35db78ed4844B34a']?.inUSD)}
                                primaryTypographyProps={{fontWeight: 700, textAlign: 'right'}}
                                secondary="USD Value"
                                secondaryTypographyProps={{textAlign: 'right'}}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemText
                                primary={CoineusCryptoFormat(formatWeiToNumber(data.coineus_fund_hedge_fund_balance)) + " Hedge Fund"}
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Balance"
                            />
                            <ListItemText
                                primary={CoineusCryptoFormat(data.coineus_fund_hedge_fund_dividends) + " FOOK"}
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Earned All-time"
                            />
                        </ListItem>
                    </List>
                </Paper>
            </Grid>

        </Grid>
    </Container>
}